.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.carousel-item img {
  height: 400px;
  /* Adjust the height of the images */
  object-fit: cover;
  /* Makes the image cover the container */
  width: 100%;
  /* Ensures the image spans the full width */
}

@media (max-width: 768px) {
  .carousel-item img {
    height: 300px;
    /* Smaller height for smaller screens */
  }
}

@media (max-width: 576px) {
  .carousel-item img {
    height: 250px;
    /* Adjust height for very small screens */
  }
}

.search-container {
  position: relative;
}

.search-input {
  height: 30px;
  border-radius: 30px;
  padding-left: 35px;
  border: none;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.search-icon {
  position: absolute;
  top: 50%;
  left: 15px;
  transform: translateY(-50%);
  color: #888;
}

.price-container {
  position: relative;
  /* Makes the container a positioned element */
  display: inline-block;
  /* Ensures the container adjusts to its content */
  padding: 10px;
  border: 1px solid #ccc;
  /* Optional: For better visibility */
  width: 100px;
  /* Set width as needed */
}

.rupee {
  position: absolute;
  /* Position the rupee value absolutely */
  top: 0;
  /* Align to the top */
  left: 0;
  /* Align to the left */
  font-size: 12px;
  /* Adjust the size as needed */
  color: gray;
  /* Optional: For styling */
}

.value {
  font-size: 18px;
  /* Larger font for the main value */
}

.error {
  color: red;
}